<template>
  <div id="app">
    <v-navbar :navList="navItems" />
    <div class="container-fluid">
      <router-view />
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import vNavbar from './components/v-navbar'

export default {
  name: 'App',
  components: {
    vNavbar
  },
  data: () => ({
    navItems: [
      { name: 'Склад', to: '/' },
      { name: 'Продажи', to: '/orders' },
      { name: 'Продукты', to: '/products' },
      { name: 'Роллы', to: '/rolls' },
      { name: 'Создать заказ', to: '/create-order' },
    ]
  }),
  methods: {
    ...mapActions(['loadRolls', 'loadStorage', 'loadOrders', 'loadProducts']),
    init() {
      this.loadRolls()
      this.loadStorage()
      this.loadOrders()
      this.loadProducts()
    }
  },
  created() {
    this.init()
  }
}
</script>

<style lang="scss">
  #app {
    height: 100vh;
  }
  .container-fluid {
    height: 94%;
